<script lang="ts">
  import { onDestroy, onMount, tick } from 'svelte';
  import { fade } from 'svelte/transition';
  import { Highlight } from 'svelte-highlight';
  import { xml } from 'svelte-highlight/languages';
  import { atomOneDark } from 'svelte-highlight/styles';
  import {
    addMessages,
    init,
    getLocaleFromNavigator,
    locale,
    locales,
    _,
  } from 'svelte-i18n';
  import { RingLoader } from 'svelte-loading-spinners';
  import { ScenePlayer, StoryPlayer } from '@storyteller/player';
  import { Replayer } from 'rrweb';
  import en from './locale/storyteller/en.json';
  import zhCN from './locale/storyteller/zh-CN.json';
  import BackgroundIcon from './icons/background.svg';
  import TooltipIcon from './icons/tooltip.svg';
  import ScissorIcon from './icons/scissor.svg';
  import MouseIcon from './icons/mouse.svg';
  import HighlightIcon from './icons/highlight.svg';
  import OperationIcon from './icons/operation.svg';
  import DemoIcon from './icons/demo.svg';
  // import TwitterIcon from './icons/twitter.svg';
  // import GithubIcon from './icons/github.svg';
  // import FacebookIcon from './icons/facebook.svg';

  let scrolled = false;
  type RGB = [number, number, number];
  let bgColor: RGB;
  let floatHeight = 20;
  let selectedFeature: 'Tooltip' | 'Cut' | 'Mouse' | 'Highlight';
  let selectedMedia: 'Web' | 'Terminal' | 'Video';
  const ControlBarHeight = 40;
  const sceneEvents = {
    en: {
      recordEvents: {
        url: 'scene-ad17f20f4f7d9bfcb616ccba33598d50.json',
        width: 1315,
        height: 822,
      },
      webviewEvents: {
        url: 'webview_en.json',
      },
      editEvents: {
        url: 'scene-023af66b77a1f1db54566e4e9da1e2b5.json',
        width: 1315,
        height: 739,
      },
      previewEvents: {
        url: 'scene-8e3b993355779e682d4b02b77efb8235.json',
        width: 1315,
        height: 739,
      },
      resultEvents: {
        url: 'scene-6807975fb9400c855e2d155609f8bc70.json',
        width: 1315,
        height: 739,
      },
      cutEvents: {
        url: 'scene-743d85fb7e2fe47b75e4760f2785faf4.json',
        width: 1920,
        height: 945,
      },
      mouseEvents: {
        url: 'scene-382dade0f60871ec90bee4c7aa5d3ff1.json',
        width: 1315,
        height: 739,
      },
      highlightEvents: {
        url: 'scene-8410bc32979e57884812395854ccf052.json',
        width: 1920,
        height: 945,
      },
      webEvents: {
        url: 'scene-5807975fb9400c855e2d155609f8bc75.json',
        width: 1315,
        height: 739,
      },
      terminalEvents: {
        url: 'scene-205a9f4b7035350ec15f103ce4f3f33a.json',
        width: 1750,
        height: 885,
      },
      videoEvents: {
        url: 'scene-6159d2088594e89c57108f62f606d39e.json',
        width: 1328,
        height: 1128,
      },
      pixelSTEvents: {
        url: 'scene-a2041ad780d782720d1fc246921e7aa0.json',
        width: 1315,
        height: 739,
      },
      pixelVideoEvents: {
        url: 'scene-fa7019118abf30357fbdb40afee0479a.json',
        width: 478,
        height: 268,
      },
      managementEvents: {
        url: 'scene-380a38a81ce385783aa90d4b69715997.json',
        width: 1315,
        height: 739,
      },
    },
    'zh-CN': {
      recordEvents: {
        url: 'scene-a5142f66502f93083d5f6617a05bf480.json',
        width: 1920,
        height: 1080,
      },
      webviewEvents: {
        url: 'webview_zh.json',
      },
      editEvents: {
        url: 'scene-a7a2343d368a3f114f3b205275cdd28f.json',
        width: 1920,
        height: 998,
      },
      previewEvents: {
        url: 'scene-63de9a5b56221a7006e2fb9d9fc2ddb7.json',
        width: 1315,
        height: 739,
      },
      resultEvents: {
        url: 'scene-abd70128699795b4d51c0a384b514aa5.json',
        width: 1920,
        height: 998,
      },
      cutEvents: {
        url: 'scene-8755fb075debc85cba336dab7264bce9.json',
        width: 1920,
        height: 945,
      },
      mouseEvents: {
        url: 'scene-fa8a73ad9c66cb362a4eb40c7cbb1fe5.json',
        width: 1920,
        height: 998,
      },
      highlightEvents: {
        url: 'scene-313d397234142589ef9039cd7ac4760d.json',
        width: 1920,
        height: 945,
      },
      webEvents: {
        url: 'scene-abd70128699795b4d51c0a384b514aa6.json',
        width: 1920,
        height: 998,
      },
      terminalEvents: {
        url: 'scene-0d7aed7f4a763eca024ef27c84feeb19.json',
        width: 1200,
        height: 610,
      },
      videoEvents: {
        url: 'scene-55b657574f1d94370665357255967257.json',
        width: 1328,
        height: 1128,
      },
      pixelSTEvents: {
        url: 'scene-dcccb45168d16e99ba55f545bb9e7bd5.json',
        width: 1315,
        height: 739,
      },
      pixelVideoEvents: {
        url: 'scene-6146f0c4ca39ffac8b4774fc79d8edd8.json',
        width: 574,
        height: 322,
      },
      managementEvents: {
        url: 'scene-106f887e0b4fde08a735b8c68917875d.json',
        width: 2296,
        height: 1288,
      },
    },
  };
  let buildPlayer,
    featurePlayer,
    pixelSTPlayer,
    pixelVideoPlayer,
    mediaPlayer,
    managementPlayer;
  let buildLoading = true,
    featureLoading = true,
    pixelSTLoading = true,
    pixelVideoLoading = true,
    mediaLoading = true,
    managementLoading = true;
  let destroyFeaturePlayer, destroyMediaPlayer;
  let pixelSTController, pixelVideoController;
  let playerMap: Record<
    'record' | 'feature' | 'media' | 'pixelST' | 'pixelVideo' | 'management',
    Partial<{
      created: boolean;
      controller: unknown;
      element: HTMLElement;
      creator: () => void;
    }>
  > = {
    record: { created: false },
    feature: { created: false },
    media: { created: false },
    pixelST: { created: false },
    pixelVideo: { created: false },
    management: { created: false },
  };
  let pixelCompareReady = false;
  $: code = `
  <iframe
    title="storyteller"
    class="border-0 shadow-xl my-4"
    src="https://storyteller.webzard.io/"
  />`;

  addMessages('en', en);
  addMessages('zh-CN', zhCN);
  init({
    fallbackLocale: 'en',
    initialLocale:
      localStorage.getItem('language') ||
      (/zh.*/.test(getLocaleFromNavigator()) ? 'zh-CN' : 'en'),
  });
  const featureSceneData = {
    Tooltip: sceneEvents[$locale].editEvents,
    Cut: sceneEvents[$locale].cutEvents,
    Mouse: sceneEvents[$locale].mouseEvents,
    Highlight: sceneEvents[$locale].highlightEvents,
  };
  const mediaSceneData = {
    Web: sceneEvents[$locale].webEvents,
    Terminal: sceneEvents[$locale].terminalEvents,
    Video: sceneEvents[$locale].videoEvents,
  };

  function handleScroll(e: Event) {
    scrolled = window.scrollY > 0;

    const k = window.scrollY / 600;
    bgColor = pickHex([236, 72, 153], [139, 92, 246], k);
    floatHeight = 20 + 40 * k;

    for (let player in playerMap) {
      const { element, controller, creator, created } = playerMap[player];
      if (element && isElementInViewport(element)) {
        if (!created) creator();
        else controller?.play();
      } else {
        controller?.pause();
      }
    }
  }

  function isElementInViewport(el) {
    const rect = el.getBoundingClientRect();
    const threshold = 50;
    return (
      rect.top >= -threshold &&
      rect.bottom <=
        (window.innerHeight || document.documentElement.clientHeight) +
          threshold
    );
  }

  async function requestScene(url: string) {
    const baseUrl = './stories/';
    const res = await fetch(baseUrl + url, {
      method: 'GET',
    });
    const json = await res.json();
    return json;
  }

  function formatScene(payload) {
    const id = (Math.random() * 10000).toString();
    const effects = {
      TOOLTIP: [],
      MOUSE: [],
      SKIP: [],
    };
    payload.effects?.forEach(e => effects[e.type].push(e));
    return {
      id,
      name: '',
      ...payload,
      mouseEffects: effects.MOUSE,
      tooltipEffects: effects.TOOLTIP,
      skipEffects: effects.SKIP,
    };
  }

  async function createFeaturePlayer() {
    if (destroyFeaturePlayer) {
      destroyFeaturePlayer();
    }
    playerMap['feature'].created = true;
    // start the loading animation
    featureLoading = true;
    const sceneData = await requestScene(featureSceneData[selectedFeature].url);
    // close the loading animation
    featureLoading = false;
    await tick();
    destroyFeaturePlayer = ScenePlayer(featurePlayer, {
      scene: formatScene(sceneData.payload),
      controlBar: true,
      width: featurePlayer.clientWidth,
      height:
        (featurePlayer.clientWidth * featureSceneData[selectedFeature].height) /
          featureSceneData[selectedFeature].width +
        ControlBarHeight,
      locale: $locale,
      options: {
        enableInteract: true,
        UNSAFE_replayCanvas: true,
        triggerFocus: false,
        insertStyleRules,
      },
      onCreate: playerObject => {
        Object.assign(playerMap['feature'], {
          element: featurePlayer,
          controller: playerObject.playerController,
        });
        playerObject.playerController.start();
        if (isElementInViewport(featurePlayer)) {
          playerObject.playerController.play();
        }
      },
    });
  }

  $: {
    if (selectedFeature && featurePlayer) {
      createFeaturePlayer();
    }
  }

  async function createMediaPlayer() {
    if (destroyMediaPlayer) {
      destroyMediaPlayer();
    }
    playerMap['media'].created = true;
    // start the loading animation
    mediaLoading = true;
    const sceneData = await requestScene(mediaSceneData[selectedMedia].url);
    // close the loading animation
    mediaLoading = false;
    await tick();

    destroyMediaPlayer = ScenePlayer(mediaPlayer, {
      scene: formatScene(sceneData.payload),
      controlBar: true,
      width: mediaPlayer.clientWidth,
      height:
        (mediaPlayer.clientWidth * mediaSceneData[selectedMedia].height) /
          mediaSceneData[selectedMedia].width +
        ControlBarHeight,
      locale: $locale,
      options: {
        enableInteract: true,
        triggerFocus: false,
        insertStyleRules,
        UNSAFE_replayCanvas: /^((?!chrome|android).)*safari/i.test(
          navigator.userAgent
        ),
      },
      onCreate: playerObject => {
        Object.assign(playerMap['media'], {
          element: mediaPlayer,
          controller: playerObject.playerController,
        });
        playerObject.playerController.start();
        if (isElementInViewport(mediaPlayer)) {
          playerObject.playerController.play();
        }
      },
    });
  }

  $: {
    if (selectedMedia && mediaPlayer) {
      createMediaPlayer();
    }
  }

  const insertStyleRules = [
    `[style*="inset: 0px"] { left: 0; top: 0; right: 0; bottom: 0 }`,
  ];

  onMount(() => {
    window.addEventListener('scroll', handleScroll, { passive: true });

    setTimeout(async () => {
      Object.assign(playerMap['record'], {
        created: true,
        element: buildPlayer,
      });
      const [
        recordEvents,
        webviewEvents,
        editEvents,
        previewEvents,
        resultEvents,
      ] = await Promise.all([
        requestScene(sceneEvents[$locale].recordEvents.url),
        requestScene(sceneEvents[$locale].webviewEvents.url),
        requestScene(sceneEvents[$locale].editEvents.url),
        requestScene(sceneEvents[$locale].previewEvents.url),
        requestScene(sceneEvents[$locale].resultEvents.url),
      ]);
      buildLoading = false;
      await tick();
      const createBuildPlayer = () => {
        let replayer;
        let webviewReplayer;
        // CreateWebviewPlayer is an async process so we need to store its promise object to be used in some other handlers.
        let createWebviewPromise;
        const WebviewCreateTimestamp =
          $locale === 'en' ? 1617187685610 : 1614850588383;
        const WebviewCreateTime =
          $locale === 'en' ? 9300.804541014833 : 6274.7066796845465;
        const WebviewIDName = 'recorder-webview';
        const sceneSize = [
          sceneEvents[$locale].recordEvents,
          sceneEvents[$locale].editEvents,
          sceneEvents[$locale].previewEvents,
          sceneEvents[$locale].resultEvents,
        ];
        const storyPlayer = new StoryPlayer(buildPlayer, {
          story: {
            id: '0',
            name: 'build',
            scenes: [
              formatScene(recordEvents.payload),
              formatScene(editEvents.payload),
              formatScene(previewEvents.payload),
              formatScene(resultEvents.payload),
            ],
          },
          controlBar: true,
          width: buildPlayer.clientWidth,
          height:
            (buildPlayer.clientWidth * sceneSize[0].height) /
              sceneSize[0].width +
            ControlBarHeight,
          autoStart: true,
          webOptions: {
            enableInteract: true,
            // if the browser vendor is safari, we need to open "allow-scripts" because of a safari bug(https://bugs.webkit.org/show_bug.cgi?id=218086)
            UNSAFE_replayCanvas: /^((?!chrome|android).)*safari/i.test(
              navigator.userAgent
            ),
            triggerFocus: false,
            insertStyleRules,
          },
        });
        function createWebviewPlayer() {
          const webviewIframe = replayer.iframe.contentDocument.getElementById(
            WebviewIDName
          );
          if (webviewIframe) {
            // On firefox, readyState of blank src iframe isn't "complete" on it's created
            createWebviewPromise = new Promise<void>(resolve => {
              // chrome and safari
              if (webviewIframe.contentDocument.readyState === 'complete')
                resolve();
              // firefox
              else {
                webviewIframe.addEventListener('load', () => resolve());
              }
            }).then(() => {
              const newDocument = webviewIframe.contentDocument;
              // insert css style of rrweb
              const styleNode = newDocument.createElement('style');
              styleNode.type = 'text/css';
              styleNode.appendChild(
                newDocument.createTextNode(
                  `html{overflow:hidden;}body{padding:0;margin:0;}.replayer-wrapper{position:relative}.replayer-mouse{position:absolute;width:20px;height:20px;transition:.05s linear;background-size:contain;background-position:50%;background-repeat:no-repeat;background-image:url('data:image/svg+xml;base64,')}.replayer-mouse:after{content:'';display:inline-block;width:20px;height:20px;border-radius:10px;background:#4950f6;transform:translate(-10px,-10px);opacity:.3}.replayer-mouse.active:after{animation:click .2s ease-in-out 1}.replayer-mouse-tail{position:absolute;pointer-events:none}@keyframes click{0%{opacity:.3;width:20px;height:20px;border-radius:10px;transform:translate(-10px,-10px)}50%{opacity:.5;width:10px;height:10px;border-radius:5px;transform:translate(-5px,-5px)}}`
                )
              );
              newDocument.head.appendChild(styleNode);
              webviewReplayer = new Replayer(webviewEvents.events, {
                root: newDocument.body,
                mouseTail: false,
              });
              webviewReplayer.enableInteract();
              webviewReplayer.play();
              createWebviewPromise = null;
            });
          }
        }
        storyPlayer
          .on('ready', () => {
            // stop infinite loading of the first iframe(https://stackoverflow.com/questions/38204722/infinite-loading-with-script-tag-inside-iframe)
            window.stop();

            if (
              storyPlayer.sceneIndex === 0 &&
              storyPlayer.playerObject.type === 'WEB'
            ) {
              Object.assign(playerMap['record'], {
                element: buildPlayer,
                controller: storyPlayer,
              });
              replayer = storyPlayer.playerObject.ref;
              replayer.on('event-cast', event => {
                if (event.timestamp === WebviewCreateTimestamp) {
                  createWebviewPlayer();
                }
                // If webview is destroyed, stop it and clear the replayer
                else if (
                  !replayer.iframe.contentDocument.getElementById(
                    WebviewIDName
                  ) &&
                  webviewReplayer
                ) {
                  webviewReplayer.pause();
                  webviewReplayer = null;
                }
              });
            }
          })
          .on('playing', () => {
            if (storyPlayer.sceneIndex === 0) {
              if (
                replayer.iframe.contentDocument.getElementById(WebviewIDName)
              ) {
                if (!webviewReplayer) {
                  createWebviewPlayer();
                } else webviewReplayer.play();
              }
              // If webview is destroyed, stop it and clear the replayer
              else if (webviewReplayer) {
                webviewReplayer.pause();
                webviewReplayer = null;
              }
            }
          })
          .on('paused', () => {
            if (
              storyPlayer.sceneIndex === 0 &&
              replayer.iframe.contentDocument.getElementById(WebviewIDName)
            ) {
              webviewReplayer.pause();
            }
          })
          .on('tooltip', () => {
            const webviewIframe = replayer.iframe.contentDocument.getElementById(
              WebviewIDName
            );
            if (storyPlayer.sceneIndex === 0 && webviewIframe) {
              // Because rrweb will destroy the webview iframe when jumping to a previous time, we need to recreate a webview player
              if (
                webviewReplayer &&
                !webviewIframe.contentDocument.contains(webviewReplayer.iframe)
              ) {
                webviewReplayer.pause();
                createWebviewPlayer();
              }

              if (createWebviewPromise)
                // createWebviewReplayer hasn't finished and need to wait
                createWebviewPromise.then(() => {
                  const tooltipTime =
                    replayer.getCurrentTime() - WebviewCreateTime;
                  // if without setTimeout, the pause(timeOffset) doesn't work
                  setTimeout(() => webviewReplayer.pause(tooltipTime), 0);
                });
              else {
                const tooltipTime =
                  replayer.getCurrentTime() - WebviewCreateTime;
                webviewReplayer.pause(tooltipTime);
              }
            }
          })
          .on('update-scene', () => {
            const sceneIndex = storyPlayer.sceneIndex;
            storyPlayer.resize({
              width: buildPlayer.clientWidth,
              height:
                (buildPlayer.clientWidth * sceneSize[sceneIndex].height) /
                  sceneSize[sceneIndex].width +
                ControlBarHeight,
            });
          });
      };
      createBuildPlayer();

      async function createPixelSTPlayer() {
        playerMap['pixelST'].created = true;
        const pixelSTEvents = await requestScene(
          sceneEvents[$locale].pixelSTEvents.url
        );
        pixelSTLoading = false;
        await tick();
        ScenePlayer(pixelSTPlayer, {
          scene: formatScene(pixelSTEvents.payload),
          controlBar: true,
          width: pixelSTPlayer.clientWidth,
          height:
            (pixelSTPlayer.clientWidth *
              sceneEvents[$locale].pixelSTEvents.height) /
              sceneEvents[$locale].pixelSTEvents.width +
            ControlBarHeight,
          locale: $locale,
          options: {
            enableInteract: true,
            triggerFocus: false,
            insertStyleRules,
          },
          onCreate: playerObject => {
            pixelSTController = playerObject.playerController;
            Object.assign(playerMap['pixelST'], {
              element: pixelSTPlayer,
              controller: playerObject.playerController,
            });
            playerObject.playerController.on('ready', () => {
              if (!pixelCompareReady) pixelCompareReady = true;
              else if (isElementInViewport(pixelSTPlayer)) {
                pixelSTController.play();
                pixelVideoController.play();
              }
            });
            playerObject.playerController.start();
          },
        });
      }
      async function createPixelVideoPlayer() {
        playerMap['pixelVideo'].created = true;
        const pixelVideoEvents = await requestScene(
          sceneEvents[$locale].pixelVideoEvents.url
        );
        pixelVideoLoading = false;
        await tick();
        ScenePlayer(pixelVideoPlayer, {
          scene: formatScene(pixelVideoEvents.payload),
          controlBar: true,
          width: pixelVideoPlayer.clientWidth,
          height:
            (pixelVideoPlayer.clientWidth *
              sceneEvents[$locale].pixelVideoEvents.height) /
              sceneEvents[$locale].pixelVideoEvents.width +
            ControlBarHeight,
          locale: $locale,
          onCreate: playerObject => {
            pixelVideoController = playerObject.playerController;
            Object.assign(playerMap['pixelVideo'], {
              element: pixelVideoPlayer,
              controller: playerObject.playerController,
            });
            playerObject.playerController.on('ready', () => {
              if (!pixelCompareReady) pixelCompareReady = true;
              else if (isElementInViewport(pixelVideoPlayer)) {
                pixelSTController.play();
                pixelVideoController.play();
              }
            });
            playerObject.playerController.start();
          },
          getPlayerObject: playerObject => {
            playerObject.ref.muted = true;
          },
        });
      }

      async function createManagementPlayer() {
        playerMap['management'].created = true;
        const managementEvents = await requestScene(
          sceneEvents[$locale].managementEvents.url
        );
        managementLoading = false;
        await tick();
        ScenePlayer(managementPlayer, {
          scene: formatScene(managementEvents.payload),
          controlBar: true,
          width: managementPlayer.clientWidth,
          height:
            (managementPlayer.clientWidth *
              sceneEvents[$locale].managementEvents.height) /
              sceneEvents[$locale].managementEvents.width +
            ControlBarHeight,
          locale: $locale,
          options: {
            triggerFocus: false,
            insertStyleRules,
            UNSAFE_replayCanvas: /^((?!chrome|android).)*safari/i.test(
              navigator.userAgent
            ),
          },
          onCreate: playerObject => {
            Object.assign(playerMap['management'], {
              element: managementPlayer,
              controller: playerObject.playerController,
            });
            playerObject.playerController.start();
            if (isElementInViewport(managementPlayer)) {
              playerObject.playerController.play();
            }
          },
        });
      }

      playerMap['pixelST'] = Object.assign(playerMap['pixelST'], {
        element: pixelSTPlayer,
        controller: null,
        creator: createPixelSTPlayer,
      });
      playerMap['pixelVideo'] = Object.assign(playerMap['pixelVideo'], {
        element: pixelVideoPlayer,
        controller: null,
        creator: createPixelVideoPlayer,
      });
      playerMap['management'] = Object.assign(playerMap['management'], {
        element: managementPlayer,
        controller: null,
        creator: createManagementPlayer,
      });
      playerMap['feature'] = Object.assign(playerMap['feature'], {
        element: featurePlayer,
        controller: null,
        creator: () => {
          if (!selectedFeature) {
            selectedFeature = 'Tooltip';
            Object.assign(playerMap['feature'], { created: true });
          }
        },
      });
      playerMap['media'] = Object.assign(playerMap['media'], {
        element: mediaPlayer,
        controller: null,
        creator: () => {
          if (!selectedMedia) {
            selectedMedia = 'Web';
            Object.assign(playerMap['media'], { created: true });
          }
        },
      });
    }, 50);
  });

  onDestroy(() => {
    window.removeEventListener('scroll', handleScroll);
  });

  function pickHex(color1: RGB, color2: RGB, weight: number): RGB {
    const w1 = weight;
    const w2 = 1 - w1;
    const rgb: RGB = [
      Math.round(color1[0] * w1 + color2[0] * w2),
      Math.round(color1[1] * w1 + color2[1] * w2),
      Math.round(color1[2] * w1 + color2[2] * w2),
    ];
    return rgb;
  }

  let email = '';
  let subscribePromise;
  function getQueryVariable(variable) {
    var query = window.location.search.substring(1);
    var vars = query.split('&');
    for (var i = 0; i < vars.length; i++) {
      var pair = vars[i].split('=');
      if (decodeURIComponent(pair[0]) == variable) {
        return decodeURIComponent(pair[1]);
      }
    }
  }
  function subscribe() {
    if (subscribePromise) {
      return;
    }
    subscribePromise = fetch(
      'https://subscription-email.smartx-labs.workers.dev/storyteller',
      {
        method: 'POST',
        body: JSON.stringify({
          email,
          custom_fields: {
            w1_T: getLocaleFromNavigator() || $locale,
            w2_T: getQueryVariable('utm_source'),
            w3_T: getQueryVariable('utm_medium'),
          },
        }),
        mode: 'cors',
      }
    )
      .then(resp => {
        if (resp.status >= 400) {
          throw new Error(resp.statusText);
        }
        email = '';
      })
      .finally(() => {
        setTimeout(() => {
          subscribePromise = null;
        }, 2000);
      });
  }
</script>

<svelte:head>
  {@html atomOneDark}
  <!-- Global site tag (gtag.js) - Google Analytics -->
  <script
    async
    src="https://www.googletagmanager.com/gtag/js?id=G-K7MYNWYD7Q"></script>
  <script>
    window.dataLayer = window.dataLayer || [];
    function gtag() {
      dataLayer.push(arguments);
